const showAllButton = document.getElementById("showAllButton");
const hideAllButton = document.getElementById("hideAllbutton");
const brands__card = document.querySelectorAll(".brands__card");

/* */
showAllButton.addEventListener("click", function () {
  brands__card.forEach((brands__card) => {
    brands__card.classList.remove("hidden");
  });
});
//  
hideAllButton.addEventListener("click", function () {
  brands__card.forEach((brands__card) => {
    brands__card.classList.add("hidden");
  });
});
